* {
  font-family: 'Open Sans', sans-serif;
}

body {
  background-image: url("../img/background.jpg");
  background-repeat: repeat;

}

.background-container {
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #fff;
  box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 1);
}

input {
  border: none;
  border-bottom: 2px solid black;
}

textarea {
  border-radius: 10px;
  border-color: black;
}

textarea:active,
input:active {
  border-color: gray;
}

.header a {
  display: block;
  margin: 0 auto;
}

.header img {
  display: block;
  width: 200px;
  height: auto;
  margin: 0 auto;
}
.header p {
  width: 80%;
  margin: 0 auto;
  // text-align: center;
}

.header h1 {
  text-align: center;
  margin-bottom: 30px;
}

.header {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.rating_block,
.rating_block--disbled{
    width: 100%;
    height: 25px;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
  }
  .rating_block input[type="radio"],
  .rating_block--disbled input[type="radio"] {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
  }
  .label_rating {
    display: block;
    width: 25px;
    height: 25px;
    background-image: url(../img/rating.png);
    background-size: cover;
    cursor: pointer;
  }

  .rating_block .label_rating:hover, /*Правило для ховера на текущий лейбл*/
.rating_block .label_rating:hover ~ .label_rating, /*Правило для всех следующих лейблов по DOM дереву*/
.rating_block input[type="radio"]:checked ~ .label_rating/*Правило для всех следующих лейблов после выбранного инпута, чтобы звездочки как бы зафиксировались*/
{
  background-image: url(../img/rating-set.png);
  background-size: cover;
}

.rating_block--disbled input[type="radio"]:checked ~ .label_rating {
  background-image: url(../img/rating-set.png);
  background-size: cover;
}

  .feedbacks {
    width: 500px;
    margin: 0 auto;
    border: 2px solid black;
  }

  .feedback {
    padding: 20px;
  }

  .feedback__username {
    font-weight: 800;
    font-size: 26px;
  }

  .feedback__text {
    margin-left: 20px;
    margin-right: 20px;
  }

  .stars .rating_block {
    margin: 0 auto;
  }

  .stars__block {
    margin-bottom: 10px;
  }

  .stars__description {
    font-size: 20px;
    font-weight: 800;
    text-align: center;
    width: 30%;
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  .feedbacks__header {
    text-align: center;
  }

  .feedback__add {
    width: 100%;
    margin: 0 auto;
    padding-bottom: 20px;
    margin-top: 50px;
  }

  .feedback__add {
    padding-left: 20px;
    padding-right: 20px;
  }

  .feedback__add h2 {
    text-align: center;
  }

  .feedback__fill {
    display: flex;
    flex-direction: column;
  }

  .feedback__fill label {
    font-size: 18px;
    font-weight: 800;
  }

  .feedback__fill input,
  .feedback__fill textarea {
    margin-bottom: 5px;
  }

  .feedback__send {
    font-size: 16px;
    display: block;
    border: none;
    margin: 0 auto;
    margin-top: 30px;
    padding: 20px;

    border: 2px solid black;
    background-color: transparent;
    border-radius: 10px;
  }

  .footer {
    border-top: 2px solid black;
    min-height: 120px;
    text-align: center;
    padding-top: 20px;
    margin-top: 10px;
  }

  .footer .copyright {
    color: black;
    text-decoration: none;
    font-size: 30px;
  }

