  width: 10% !important;
}

.carousel-inner {
  text-align: center;
}

@media (min-width: 1140px) {
  .feedback__add {
    width: 70%;
  }
}

@media (min-width: 320px) {
  .header p {
    width: 100%;
  }
}

@media (min-width: 576px) {
  .header p {
    width: 80%;
  }
}